import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom/cjs/react-router-dom.min';

function Detail(props) {
  let { id } = useParams();
  let shoe = props.shoes.find((shoe) => shoe.id == id );
  let history = useHistory();
  let [alert, alert] = useState(true);

  useEffect(() => {
    let timer = setTimeout(() => { alert(false) }, 2000);

    return () => { clearTimeout(timer); }
  }, []);
  return (
  <div className="container">
    {
      alert === true
      ? (
        <div className='alert'>
          <p>   .</p>
        </div>
      )
      : null
    }
    <div className="row">
      <div className="col-md-6">
        <img src={`https://codingapple1.github.io/shop/shoes${parseInt(id) + 1}.jpg`} width="100%" />
      </div>
      <div className="detail-content col-md-6 mt-4">
        <h4 className="shoe-title">{ shoe.title }</h4>
        <p className='shoe-content'>{ shoe.content }</p>
        <p>{ shoe.price }</p>
        <button className="btn btn-danger" onClick={() => {
          props.dispatch({type: '', payload: {id:id, name:shoe.title, price:shoe.price, quantity: 1}});
          history.push('/cart');
        }}></button> 
      </div>
    </div>
  </div> 
  );
}

function stateprops(state) {
  return {
      state: state
  }
}

export default connect(stateprops)(Detail);